import React from 'react'
import {Table} from 'react-bootstrap'
import Pagination from 'rc-pagination'
import DeleteDevice from '../../../widgets/delete.jsx'
export default React.createClass({
  propTypes: {
    data: React.PropTypes.array.isRequired,
    pageChange: React.PropTypes.func.isRequired,
    reload:React.PropTypes.func,
    actions:React.PropTypes.object,
    total:React.PropTypes.number,
    isDeleted:React.PropTypes.bool
  },

  getDefaultProps() {
    return {
      data:[]
    }
  },

  getInitialState() {
    return {
      pageID:1,
      pageSize:10
    }
  },

  onPageChange(current) {
    this.setState({
      pageID: current
    })

    this.props.pageChange(
      current,
      this.state.pageSize
    )
  },

  delDevice(devId) {
    this.props.actions.deviceDel({
      devId:devId
    })
    this.props.reload()
  },

  changePageSize() {

  },

  render() {
    return (
        <div className="panel panel-info">
          <div className="panel-body">
            <Table hover className="text_center">
              <thead>
              <tr>
                <th className="text_center"></th>
                <th className="text_center">IP</th>
                <th className="text_center">IP</th>
                <th className="text_center"></th>
                <th className="text_center"></th>
                <th className="text_center"></th>
                <th className="text_center"></th>
                <th className="text_center"></th>
              </tr>
              </thead>

              <tbody>
              {
                this.props.data.map((item)=>{
                  let privateIp = ''
                  let publicIp = ''
                  item.deviceIPList.map((deviceIp)=>{
                    if (deviceIp.type === 0) {
                      privateIp = deviceIp.ip
                    } else {
                      publicIp = publicIp + ' ' + deviceIp.ip
                    }
                  })

                  return (
                    <tr key={item.devId}>
                      <td>{item.devId}</td>
                      <td>{privateIp}</td>
                      <td>{publicIp}</td>
                      <td>{item.hostName}</td>
                      <td>{item.idcName}</td>
                      <td>{item.busiName}</td>
                      <td>{item.moduleName}</td>
                      <td>
                        <a href={'#/configuration/devices/add_update/' + item.id}></a>
                          &nbsp;&nbsp;&nbsp;
                        <DeleteDevice itemId={item.id} btntext=""
                                title=""
                                prompt=""
                                delfunc={this.delDevice}
                        />
                      </td>
                    </tr>
                  )
                })
              }
              </tbody>
            </Table>
          </div>
          <Pagination total={this.props.total}
                      current={this.state.pageID}
                      pageSize={this.state.pageSize}
                      onChange={this.onPageChange}
                      pageSizeOptions={['10', '20', '50']}
                      onShowSizeChange={this.changePageSize}
          />
        </div>
    )
  }
})



